// group .. stati .. post ..
.info
	margin-top: 20px
	+clr
.info__link
	display: block
	margin-bottom: 5px
	span
		font-size: 18px
		color: $blue
		+os
	&:hover
		span
			text-decoration: underline
	&:active
		span
			color: #2693BF

.article
	background-color: #fff
	margin-bottom: 13px
	+clr
	.title__main
		margin: 5px 0 15px
.article__container
	padding: 35px 44px
	+box
	+clr
.article__text
	+clr
	p
		font-size: 18px
		color: $black
		+osl
.article__img
	float: left
	max-width: 100%
	margin: 30px 15px 30px 0
	img
		max-width: 100%
.article__picture
	float: left
	width: 105px
	height: 105px
	margin: 0 30px 10px 0
	img
		max-width: 100%
		vertical-align: top
		-webkit-border-radius: 100%
		border-radius: 100%
.article__footer
	border-top: 1px solid #ecf0f4
	background-color: #FFF
	+clr
	.seller
		display: inline-block
		margin-bottom: 0
		transform: translate(0, -50%)
		padding-left: 45px
		margin-left: 0
	.seller__text
		white-space: nowrap
	.seller__img:after
		content: none
	.say
		padding-left: 45px
		transform: translate(0, -50%)
.article__centering
	margin-top: 50px
	transform: translate(-50%, -50%)
	margin-left: 50%
	// transform: translate(-50%)
.article__name
	float: left
	border-right: 1px solid #ecf0f4
	width: 23%
	height: 100px
	// padding-left: 45px
	+box
.article__tag
	float: left
	height: 100px
	width: 16.5%
	white-space: nowrap
	border-right: 1px solid #ecf0f4
	p
		font-size: 14px
		color: #727376
		+osl

.article__rating
	float: left
	width: 11%
	height: 100px
	white-space: nowrap
	border-right: 1px solid #ecf0f4
	&:hover,
	&.is_active
		.icon-star
			+s(icon-star-active)
		.icon-cool
			+s(icon-cool-active)
		.icon-mes
			+s(icon-mes-active)
	span
		display: inline-block
		font-size: 18px
		color: $black
		+os
		vertical-align: top
		margin-left: 8px

.article__button
	float: left
	height: 100px
	width: 25%
	white-space: nowrap
	p
		font-size: 14px
		+osl
		color: #9fa0a1
	a
		font-size: 14px
		display: block
		color: $blue
		+osl

.group__footer
	border-top: 1px solid #ecf0f4
	background-color: #FFF
	+clr
	.article__centering
		display: inline-block

.say
	float: left
	width: 21%
	height: 100px
	border-right: 1px solid #ecf0f4
	font-size: 14px
	color: $black
	white-space: nowrap
	+os
.article__soc
	width: 10%
	height: 100px
	border-right: 1px solid #ecf0f4
	float: left
	&:hover
		opacity: .7

.group__thame
	width: 39%
	float: left
	height: 100px

.group__thame_wrap
	white-space: nowrap
	+clr
	a
		font-size: 14px
		display: inline-block
		color: $blue
		+os
		margin-right: 10px
		padding: 1px 3px
		line-height: 1
		&:hover
			background-color: #E8ECEF

.pre-comment
	margin: 60px 0 55px
	+clr
	h4
		float: left
		font-size: 30px
		color: $black
		white-space: nowrap
		padding-left: 45px
		+osl

.pre-comment_btn
	float: right
	white-space: nowrap
	+clr
	.btn
		margin-left: 10px
		vertical-align: top


.commentar
	position: relative
	padding: 35px 0px 30px 40px
	margin-bottom: 15px
	background-color: #FFF
	+clr

.commentar__benefit,
.subcommentar__benefit
	position: absolute
	top: 35px
	right: 35px
	a
		float: left
		vertical-align: top
	span
		float: left
		margin: 0 10px
		vertical-align: top
		font-size: 15px
		color: #ADAEB3
		+osl
.icon-minus
	&:hover
		+s(icon-minus-h)

.icon-plus
	&:hover
		+s(icon-plus-h)

.subcommentar__benefit
	position: absolute
	top: 35px
	right: -80px

.commentarium
	position: relative
	padding-left: 70px
	+clr


.commentar__form
	position: relative
	top: -1px
	padding: 0
	margin: 0
	border: 0
	outline: 0
	width: 91%
	textarea
		width: 100%
		height: 203px
		border: 1px solid #e6eaed
		top: 0
		margin-right: 9%
		resize: none
		padding: 10px
		&:hover
			border-color: #c6ccd1
	.btn
		float: right
		margin: 20px 0
.commentar__user
	position: absolute
	top: 0
	left: 0
	width: 45px
	height: 45px
	vertical-align: top
	img
		max-width: 100%
		vertical-align: top
		-webkit-border-radius: 100%
		border-radius: 100%
.commentar__item
	float: left
	margin-right: 115px
	vertical-align: top
	+clr
.commentar__header
	+clr
.commentar__name
	display: inline-block
	font-size: 14px
	color: $blue
	+os
	&:hover
		color: $blued
.commentar__time
	display: inline-block
	font-size: 14px
	color: #7f8187
	+osl
.commentar__text
	font-size: 18px
	line-height: 28px
	margin: 5px 0
	color: $black
	+osl
.commentar__btn
	a
		font-size: 14px
		color: $blue
		float: left
		margin-right: 15px
		+osl
		&:hover
			color: $blued
	+clr

.subcommentar
	position: relative
	padding: 25px 0 10px 0
	+clr

.subcommentar__user
	@extend .commentar__user
	position: absolute
	top: 25px
	left: 0
.subcommentar__item
	margin-left: 70px
	+clr

.commentar__editor
	margin-right: 9%
	border: 1px solid #e6eaed
	height: 50px
	+clr
.commentar__centering
	margin-top: 25px
	-webkit-transform: translate(-50%, -50%)
	-ms-transform: translate(-50%, -50%)
	transform: translate(-50%, -50%)
	margin-left: 50%
	white-space: nowrap
	a
		display: block
.commentar__editor_one
	float: left
	height: 50px
	width: 8%
	border-right: 1px solid #e6eaed
.commentar__editor_two
	float: left
	width: 16%
	height: 50px
	border-right: 1px solid #e6eaed
	a
		display: inline-block !important
		vertical-align: top
.commentar__editor_five
	float: left
	height: 50px
	width: 27%
	border-right: 1px solid #e6eaed
	a
		vertical-align: middle
		margin-right: 20px
		display: inline-block
		&:last-child
			margin-right: 0

.state
	.article__name
		width: 28%
		p
			font-size: 14px
			color: #9FA0A1
			+osl
	.article__rating
		width: 17%
	.article__button
		width: 11%
